export default {
	props:{
		isDisabled:{
			type:Boolean,
			default:false
		}
	},
	data() {
		return {
			InputBottom: 0,
			isFocus:false
		};
	},
	methods: {
		search(){
			if(this.isDisabled){
				uni.navigateTo({
				url:'../search/search'
			})
			}
			
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
			
			
		},
		InputBlur(e) {
			this.InputBottom = 0
		}
	}
}
